.tabs {
  overflow: hidden;
}

.tabs h2 {
  color: #fff;
  margin-left: 8px;
}

.tabs__item .tabs__bl {
  min-height: 240px;
}

.tabs__item h3 {
  margin: 1.5rem 0 0 0.5rem;
  letter-spacing: 0.1px;
}

.tabs__items {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

.links {
  display: flex;
  list-style-type: none;
  margin-bottom: 2.5rem;
  overflow-x: scroll;
  scrollbar-width: none;
}

.links li {
  min-width: fit-content;
  list-style: none;
  margin-bottom: 22px;
  margin-right: 3.65vw;
}

.links li:before {
  content: "";
  display: none;
}

.links__link a {
  color: #009EE3;
  text-decoration: none;
  border: none;
  font-size: 24px;
}

.links__link.ui-tabs-active a {
  color: #373939;
  border-bottom: 2px solid;
  padding-bottom: 20px;
}

/* Let's get this party started */

::-webkit-scrollbar {
  width: 0px;
  height: 0;
}

@media only screen and (max-width: 1024px) {
  .tabs__items {
    display: grid;
    grid-template-columns: 1fr;
  }
}